<template>
  <div class="shrink-view" :style="{maxHeight: (mIsOpen?contentHeight:0) + 'px'}">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    value: Boolean //  valuev-model
  },
  updated () {
    this.init()
  },
  mounted () {
    this.init()
  },
  methods: {
    //  updated  mounted  nextTick 
    init () {
      this.$nextTick(() => {
        this.contentHeight = this.$el.scrollHeight
      })
    }
  },
  watch: {
    //  value   mIsOpen 
    value (newValue) {
      this.mIsOpen = newValue
    },
    //  mIsOpen inputv-model
    mIsOpen (newValue) {
      this.$emit('input', newValue)
    }
  },
  data () {
    return {
      contentHeight: 0, // 
      mIsOpen: this.value // prop(v-model)
    }
  }

}
</script>

<style lang="stylus" scoped>
.shrink-view {
  overflow: hidden;
  -webkit-transition-duration: 300ms;
  -moz-transition-duration: 300ms;
  -ms-transition-duration: 300ms;
  -o-transition-duration: 300ms;
  transition-duration: 300ms;
}
</style>



