          </el-tab-pane>-->
          <el-tab-pane label="" name="0" @click="curr = '0'">
            <div class="wrap">
              <el-form :label-position="labelPosition" label-width="80px" :model="formDisposalTeam">
                <el-form-item label="">
                  <el-input v-model="formDisposalTeam.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formDisposalTeam.number" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formDisposalTeam.teamType" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formDisposalTeam.personcharge" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formDisposalTeam.phone" placeholder=""></el-input>
                </el-form-item>
              </el-form>
              <div style="width:100%;text-align:center">
                <el-button type="primary" @click="addSource('team')"></el-button>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="1" @click="curr = '1'">
            <div class="wrap">
              <el-form
                :label-position="labelPosition"
                label-width="80px"
                :model="formInvokeMaterial"
              >
                <el-form-item label="">
                  <el-input v-model="formInvokeMaterial.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formInvokeMaterial.type" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formInvokeMaterial.number" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formInvokeMaterial.model" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formInvokeMaterial.warehouse" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
