                </el-form-item>
              </el-form>
              <div style="width:100%;text-align:center">
                <el-button type="primary" @click="addSource('goods')"></el-button>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="2" @click="curr = '2'">
            <div class="wrap">
              <el-form :label-position="labelPosition" label-width="80px" :model="formExpert">
                <el-form-item label="">
                  <el-input v-model="formExpert.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formExpert.type" placeholder=""></el-input>
                </el-form-item>
                <!-- <el-form-item label="">
                  <el-input v-model="formExpert.fullname" placeholder=""></el-input>
                </el-form-item>-->
