                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formCars.zbwz" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formCars.driver" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formCars.phone" placeholder=""></el-input>
                </el-form-item>
              </el-form>
              <div style="width:100%;text-align:center">
                <el-button type="primary" @click="addSource('car')"></el-button>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="4" @click="curr = '4'">
            <div class="wrap">
              <el-form
                :label-position="labelPosition"
                label-width="80px"
                :model="formHelpEnterprise"
              >
                <el-form-item label="">
                  <el-input
                    v-model="formHelpEnterprise.businessRepresentative"
                    placeholder=""
                  ></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.address" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.type" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.phone" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.respectiveRegion" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formHelpEnterprise.emile" placeholder=""></el-input>
                </el-form-item>
              </el-form>
              <div style="width:100%;text-align:center">
                <el-button type="primary" @click="addSource('company')"></el-button>
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import { eventResourceAllocate, eventRescueTeamLocation, eventRescueCar } from '../../api/visual'
export default {
  data () {
    return {
      curr: '0',
      tabPosition: 'left',
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        personcharge: '',
        address: '',
        emile: '',
        phone: ''
      },
      // 
      formDisposalTeam: {
        name: '',
        number: '',
        personcharge: '',
        teamType: '',
        phone: ''
      },
      // 
      formInvokeMaterial: {
        name: '',
        type: '',
        number: '',
        model: '',
        warehouse: '',
        people: ''
      },
      // 
      formExpert: {
        name: '',
        type: '',
        fullname: '',
        phone: '',
        education: '',
        emile: ''
      },
      // 
      formCars: {
        carnumber: '',
        type: '',
        zbwz: '',
        driver: '',
        phone: ''
      },
      // 
      formHelpEnterprise: {
        businessRepresentative: '',
        name: '',
        address: '',
        type: '',
        phone: '',
        respectiveRegion: '',
        emile: ''
      }
    }
  },
  props: {
    type: {
      type: String,
      default: ''
    },
    row: {
      type: Object,
      default: {}
    }
  },
  methods: {
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    // 
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    async addSource (type) {
      let eventId = this.$parent.eventId
      if (!eventId) {
        eventId = this.$parent.$parent.eventId
      }
      const configuration = await get({ url: 'static/config.json', data: {} })

      //  
      var param = { id: eventId, allocateContent: '' }
      //  (jg),(team)(goods), (expert), (car), (company)
      let table = {}
      if (type === 'team') {
        param.allocateContent = '' + this.formDisposalTeam.name
        let TableData = {
          id: this.getuuid(),
          // 
          eventId: eventId,
          // 
          ranksId: '',
          // 
          ranksName: this.formDisposalTeam.name,
          // 
          ranksType: this.formDisposalTeam.teamType,
          // 
          ranksDesc: '',
          // 
          person: this.formDisposalTeam.personcharge,
          // 
          tel: this.formDisposalTeam.phone,
          //  0 1 
          Dispatch: 1,
          // 
          DispatchTime: this.getNowTime(),
          // 
          CreateTime: this.getNowTime(),
          //   0
          Matchimg: 0,
          // 
          email: '',
          // 
          personNumber: this.formDisposalTeam.number
        }
        table = builderParams.save('ranks', TableData)
      }
      if (type === 'goods') {
        param.allocateContent = '' + this.formInvokeMaterial.name + ', ' + this.formInvokeMaterial.number

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 
          restype: this.formInvokeMaterial.type,
          // 
          resName: this.formInvokeMaterial.name,
          // 
          dispatchCount: this.formInvokeMaterial.number,
          // 
          specification: this.formInvokeMaterial.model,
          // 
          warehouse: this.formInvokeMaterial.warehouse,
          // 
          operator: this.formInvokeMaterial.people,
          // 
          dispatchTime: this.getNowTime(),
          // 
          createTime: this.getNowTime(),
          // 
          matching: 0
        }
        table = builderParams.save('RESOURE', item)
      }
      if (type === 'expert') {
        param.allocateContent = '' + this.formExpert.name

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 
          expertType: this.formExpert.type,
          // 
          expertName: this.formExpert.name,
          // 
          tel: this.formExpert.phone,
          // 
          education: this.formExpert.education,
          // 
          email: this.formExpert.emile,
          // 
          matching: 0,
          // 
          dispatch: 1,
          // 
          dispatchTime: this.getNowTime(),
          // 
          createTime: this.getNowTime()
        }
        table = builderParams.save('exper', item)
      }
      if (type === 'car') {
        param.allocateContent = '' + this.formCars.carnumber

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          carId: this.formCars.carnumber,
          // 
          carName: this.formCars.carnumber,
          // 
          carsType: this.formCars.type,
          // 
          goods: this.formCars.zbwz,
          // 
          cardriver: this.formCars.driver,
          // 
          tel: this.formCars.phone,
          // 
          dispatchTime: this.getNowTime(),
          // 
          createTime: this.getNowTime(),
          // 
          dispatch: 1,
          // 
          matching: 0
        }
        table = builderParams.save('car', item)
      }
      if (type === 'company') {
        param.allocateContent = '' + this.formHelpEnterprise.name

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 
          enterpname: this.formHelpEnterprise.name,
          // 
          enterpAddress: this.formHelpEnterprise.address,
          // 
          enterpdelegate: this.formHelpEnterprise.businessRepresentative,
          // 
          enterpType: this.formHelpEnterprise.type,
          // 
          tel: this.formHelpEnterprise.phone,
          // 
          area: this.formHelpEnterprise.respectiveRegion,
          // 
          email: this.formHelpEnterprise.emile,
          // 
          matching: 0,
          // 
          dispatch: 1
        }
        table = builderParams.save('enterp', item)
      }
      if (table !== {}) {
        const result = await post({ url: builderParams.execUrl(), data: table })
        if (result.Result === 0) {
          const eventResourceAllocate = configuration.visual.baseUrl + configuration.visual.eventResourceAllocate
          //   
          var quartzItem = {
            // 
            bh: this.getuuid(),
            // 
            xtmc: 'zhdd',
            // 
            jkdz: eventResourceAllocate,
            // 
            ajaxType: 'POST',
            // 
            param: JSON.stringify(param),
            // 
            state: 0,
            // 
            rksjc: this.getNowTime(),
            // 
            gxsj: null,
            // 
            gxrbh: '',
            // 
            gxr: '',
            header: '{"Authorization":"emer_test","Signature":"aedd64f062dba1ab2eb53c4977921586","Timestamp":"1574334059"}'
          }

          if (type !== 'team' && type !== 'car') {
            const task = builderParams.save('quartz', quartzItem)
            task.DataBase = 'duty'
            //  
            const taskResult = await post({ url: builderParams.execUrl(), data: task })
          }
          if (type === 'team') {
            //   -- eventRescueTeamLocation
            const eventRescueTeamLocation = configuration.visual.baseUrl + configuration.visual.eventRescueTeamLocation
            quartzItem.jkdz = eventRescueTeamLocation
            var teamParmeter = {
              eventId: eventId,
              rescueTeamId: '',
              startLongitude: '',
              startLatitude: '',
              endLongitude: '',
              endLatitude: '',
              currentLongitude: '',
              currentLatitude: '',
              status: '',
              remark: '1'
            }
            quartzItem.param = JSON.stringify(teamParmeter)
            let teamItem = builderParams.save('quartz', quartzItem)
            teamItem.DataBase = 'duty'
            const taskResult = await post({ url: builderParams.execUrl(), data: teamItem })
          }
          if (type === 'car') {
            // -
            const eventRescueCar = configuration.visual.baseUrl + configuration.visual.eventRescueCar
            quartzItem.jkdz = eventRescueCar
            var carParmeter = {
              eventId: eventId,
              plateNum: this.formCars.carnumber,
              type: this.formCars.type,
              // 
              org: '',
              content: this.formCars.zbwz,
              driver: this.formCars.driver,
              driverPhone: this.formCars.phone,
              startLongitude: '',
              startLatitude: '',
              endLongitude: '',
              endLatitude: '',
              currentLongitude: '',
              currentLatitude: '',
              status: '',
              // 
              remark: ''
            }
            quartzItem.param = JSON.stringify(carParmeter)
            let carItem = builderParams.save('quartz', quartzItem)
            carItem.DataBase = 'duty'
            const taskResult = await post({ url: builderParams.execUrl(), data: carItem })
          }
          alert('')
          // 
          if (this.$parent.eventId) {
            this.$parent.loadDispatch()
          }
          if (this.$route.name === 'command') {
            this.$parent.$parent.loadDispatch()
          }
          // 
          this.$layer.closeAll()
        }
      }
    },
    loadinit () {
      if (this.type === 'goods') {
        this.curr = '1'
        this.formInvokeMaterial.name = this.row.address,
        this.formInvokeMaterial.type = this.row.goodsType,
        this.formInvokeMaterial.number = this.row.goodsNum,
        this.formInvokeMaterial.model = this.row.goodsSpec,
        this.formInvokeMaterial.warehouse = this.row.stort,
        this.formInvokeMaterial.people = ''
      }
      if (this.type === 'expert') {
        this.curr = '2'
        this.formExpert.name = this.row.name,
        this.formExpert.type = this.row.expertCode,
        this.formExpert.fullname = this.row.name,
        this.formExpert.phone = this.row.telephone,
        this.formExpert.education = this.row.education,
        this.formExpert.emile = this.row.email
      }
      if (this.type === 'team') {
        this.curr = '0'
        this.formDisposalTeam.name = this.row.name
        this.formDisposalTeam.number = this.row.teamNum
        this.formDisposalTeam.personcharge = this.row.header
        this.formDisposalTeam.teamType = this.row.teamType
        this.formDisposalTeam.phone = this.row.headerTel
      }
    }
  },
  mounted () {
    this.loadinit()
  },
  components: {

  }

}
</script>
<style lang="stylus">
/* element ui */
/* input */
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6) !important;
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6) !important;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #fff;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append, .el-input-group__prepend {
  background-color: #1F97E9 !important;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1F97E9 !important;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #EBBB2F;
  background: #234A76 !important;
}

.res-seach .el-tabs__item {
  padding: 0 20px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #7EC8ED;
  position: relative;
}

.el-tabs__nav-wrap::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  /* background-color: #E4E7ED; */
  z-index: 1;
}

/* tab */
.el-tab-pane {
  height: 490px;
  background: #1f436d;
  color: #fff;
  padding: 14px 22px 0 0px;
}

.el-form-item__label {
  text-align: right;
  vertical-align: middle;
  float: left;
  font-size: 14px;
  color: #fff;
  line-height: 40px;
  padding: 0 8px 0 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
</style>
<style lang="stylus" scoped>
.addresources-wrap {
  width: 100%;
  height: 100%;

  .res-seach {
    width: 100%;
    height: 490px;
    margin: 12px 0 0 0px;
  }
}
</style>



