import { businessConfig } from '@/utils/config'

var database = 'yjzh'

const params = {
  /**
   *  businessConfig key
   * @param {any} key key
   */
  init: function (key) {
    database = key
  },
  /**
   *  businessConfig key
   *
   */
  initSelf: function () {
    var conf = businessConfig.getConfig()
    database = conf.Database
  },
  /*
   *  key
   **/
  switchYhtx: function () {
    var conf = businessConfig.getConfig()
    database = conf.Yhtxdatabase
  },
  /**
   *  key
   * */
  getDatabase: function () {
    if (database === '') {
      var conf = businessConfig.getConfig()
      return conf.Database
    } else {
      return database
    }
  },
  /**
   * 
   * @param {any} worktype  Save Update Delete Get
   * @param {any} tablename  t_person  person
   * @param {any} tabledata 
   * @param {any} conditions  
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  base: function (worktype, tablename, tabledata, conditions) {
    var item = {
      WorkType: worktype,
      TableName: tablename,
      TableData: tabledata,
      Conditions: conditions
    }
    return item
  },
  /**
   * 
   * @param {any} tablename  t_person  person
   * @param {any} item 
   */
  basesave: function (tablename, item) {
    return params.base('Save', tablename, item, null)
  },
  /**
   * 
   * @param {any} tablename
   * @param {any} item
   * @param {any} conditions
   */
  baseupdate: function (tablename, item, conditions) {
    return params.base('Update', tablename, item, conditions)
  },
  /**
   * 
   * @param {any} tablename
   * @param {any} conditions
   */
  basedelete: function (tablename, conditions) {
    return params.base('Delete', tablename, null, conditions)
  },
  /**
   * 
   * @param {any} list  
   */
  tranparams: function (list) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTableList = list
    return table
  },
  /**
   * 
   * @param {any} tablename  t_person  person
   * @param {any} item  
   *  s_id id
   * 
  * var item = {
        Id :"1",
        Name: "2"
    }
  * 
  * var item ={}
      item.Id = "1"
      item.Name = "2"
   */
  save: function (tablename, item) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.basesave(tablename, item)
    return table
  },
  /**
   * 
   * @param {any} tablename  t_person  person
   * @param {any} item 
   *  s_id id
   * 
  * var item = {
        Id :"1",
        Name: "2"
    }
  * 
  * var item ={}
      item.Id = "1"
      item.Name = "2"
   *
   * @param {any} conditions  
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  update: function (tablename, item, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.baseupdate(tablename, item, conditions)
    return table
  },
  /**
   * 
   * @param {any} tablename   t_person  person
   * @param {any} conditions 
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  delete: function (tablename, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.basedelete(tablename, conditions)
    return table
  },
  /**
   * 
   * @param {any} tablename   t_person  person
   * @param {any} fields 
   * @param {any} conditions 
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  get: function (tablename, fields, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.base('Get', tablename, null, conditions)
    return table
  },
  /**
   * 
   * @param {any} tablename  t_person  person
   * @param {any} fields  
   * @param {any} order  s_id id
   * @param {any} sort  desc  asc
   * @param {any} conditions 
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  query: function (tablename, fields, order, sort, conditions, type) {
    if (type === undefined || type === null) {
      type = 'Table'
    }
    var table = {}
    table.DataBase = params.getDatabase()
    var para = {}
    para.Type = type
    para.TableView = tablename
    para.Fields = fields
    para.QueryArgs = {
      Order: order,
      Sort: sort,
      Conditions: conditions
    }
    table.DynamicQuery = para
    return table
  },
  /**
   * ()
   * @param {any} tablename   t_person  person
   * @param {any} fields 
   * @param {any} order  s_id id
   * @param {any} sort  desc  asc
   * @param {any} conditions 
   * 
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  getListTable: function (tablename, fields, order, sort, conditions) {
    var item = {
      DataBase: params.getDatabase(),
      Type: 'Table',
      TableView: tablename,
      Fields: fields,
      Order: order,
      Sort: sort,
      PageIndex: 1,
      PageSize: 10,
      Conditions: JSON.stringify(conditions)
    }
    return item
  },
  /**
  * ()
  * @param {any} tablename   t_person  person
  * @param {any} fields 
  * @param {any} order  s_id id
  * @param {any} sort  desc  asc
  * @param {any} conditions 
  * 
  * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
  */
  getListView: function (tablename, fields, order, sort, conditions) {
    var item = {
      DataBase: params.getDatabase(),
      Type: 'View',
      TableView: tablename,
      Fields: fields,
      Order: order,
      Sort: sort,
      PageIndex: 1,
      PageSize: 10,
      Conditions: JSON.stringify(conditions)
    }
    return item
  },
  /**
   * 
   * @param {any} procname 
   * @param {any} inparams   []
   * @param {any} outparams   []
   */
  procedure: function (procname, inparms, outparms) {
    var table = {}
    table.DataBase = params.getDatabase()
    var para = {
      ProcName: procname,
      InParms: inparms,
      OutParms: outparms
    }
    table.DynamicProcedure = para
    return table
  },
  /**
   * 
   * @param {any} procname 
   * @param {any} inparms  json
   * @param {any} outparms  json
   * @param {any} parmname  json
   */
  procedurePaging: function (procname, inparms, outparms, parmname) {
    var item = {
      DataBase: params.getDatabase(),
      ProcName: procname,
      InParms: inparms,
      OutParms: outparms,
      ParmNames: parmname
    }
    return item
  },
  /**
   * Execute
   * */
  execUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/execute'// urlsave update delete
  },
  /**
  * TranExecute
  * */
  tranExecUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/tranexecute'// urlsave update delete
  },
  /**
   * Get
   * */
  getUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/get'// Geturl
  },
  /**
   * Query
   * */
  queryUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/query'// url
  },
  /**
   * GetList
   * */
  getListUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/getlist'// url 
  },
  /**
   * ExecuteProcedure
   * */
  execProcedureUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/executeprocedure'// ExecuteProcedure 
  },
  /**
   * ExecuteProcedurePaging
   * */
  execProcedurePageUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/executeprocedurepaging'// ExecuteProcedurePaging 
  },
  /**
   * UploadFiles 
   * */
  uploadFilesUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/attachment/uploadfiles'// uploadfiles
  },
  /**
   * 
   * */
  downloadFileUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/attachment/downloadfile'// downloadfile
  }
}
const builderParams = params

export { builderParams }



