import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 
const Home = () => import('../pages/Home') // 
const Scheme = () => import('../pages/Scheme') // 
const Command = () => import('../pages/Command') // 
const Dispose = () => import('../pages/Dispose') // 
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      name: 'home',
      path: '/home',
      component: Home
    },
    {
      name: 'scheme',
      path: '/scheme',
      component: Scheme
    },
    {
      name: 'command', // 
      path: '/command',
      component: Command
    },
    {
      name: 'dispose', // 
      path: '/dispose',
      component: Dispose
    }
  ]
})



