var mapHandle = {
  getMap () {
    return window.$M
  },
  // 
  checkLoadState (callbcak) {
    if (window.$M === undefined) {
      setTimeout(() => {
        this.checkLoadState(callbcak)
      }, 500)
    }
    if (window.$M !== undefined) {
      callbcak()
    }
  },
  // 
  switchMapServer: function (mode) { //  1  2  4 
    mapHandle.getMap().switchMapServer(mode)
  },
  // 
  zoomMeterage: function (mode) { // measure   measureArea 
    mapHandle.getMap().zoomMeterage(mode)
  },
  // 
  zoomIn: function () {
    mapHandle.getMap().zoomIn()
  },
  // 
  zoomOut: function () {
    mapHandle.getMap().zoomOut()
  },
  // 
  drawGraphic: function (option) {
    if (option) {
      var defaultOption = {
        // sid: '0001',
        tagert: null,
        // text: "",
        url: '#',
        mode: 1, //  1 point, 2 line, 3 polygon, 4 rectangle, 9 circle
        isWindow: false,
        isCenter: false,
        layerId: 'glayer',
        click: '',
        onsuccess: '',
        // ico: { name: "Images/marker/marker.png", height: 30, width: 22 },
        style: {
          color: '#000000',
          fontSize: 12,
          lineStyle: 'solid',
          opacity: 0.8,
          borderWidth: 1,
          borderColor: '#000000',
          background: '#ffffff',
          fillStyle: 'solid' // backward-diagonal,cross,diagonal-cross,forward-diagonal,horizontal,none,solid,vertical
        }
      }
      mapHandle.expand(defaultOption, option)
      mapHandle.getMap().listenerDrawGraphic(defaultOption)
    }
  },
  // 
  drawPointToMap: function (option) {
    if (option) {
      var defaultOption = {
        tagert: null,
        text: '',
        url: '#',
        // x: 0,
        // y: 0,
        isCenter: false,
        layerId: 'glayer',
        click: '',
        onsuccess: ''
        // ico: { name: cremapHandle.getMap().ExtendObj.baseDir + "Images/marker/marker.png", height: 30, width: 22 }
      }
      mapHandle.expand(defaultOption, option)
      this.checkLoadState(() => {
        mapHandle.getMap().drawPointToMap(defaultOption)
      })
    }
  },
  // 
  drawCircle: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      // text: "",
      url: '#',
      // x: 0.0,
      // y: 0.0,
      radius: option.radius, // /
      isCenter: false,
      //  layerId: "glayer",
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'dash'
      }
    }
    mapHandle.expand(defaultOption, option)
    this.checkLoadState(() => {
      mapHandle.getMap().drawCircle(defaultOption)
    })
  },
  // 
  drawPolygon: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      text: '',
      url: '#',
      pointlist: '', // // WKT
      isCenter: false,
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        opacity: 0.8,
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'solid'
      }
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().drawPolygon(defaultOption)
  },
  drawLine: function (option) {
    var defaultOption = {
      sid: '0001',
      tagert: null,
      text: '',
      url: '#',
      pointlist: '', // // WKT
      isCenter: false,
      layerId: 'glayer',
      click: '',
      onsuccess: '',
      style: {
        color: '#000000',
        fontSize: 12,
        lineStyle: 'solid',
        opacity: 0.8,
        borderWidth: 1,
        borderColor: '#000000',
        background: '#ffffff',
        fillStyle: 'solid'
      }
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().drawLineToMap(defaultOption)
  },
  // 
  // ""
  // { layerId: '', onsuccess: "" }
  clearLayers: function (option) {
    this.checkLoadState(() => {
      mapHandle.getMap().clearLayers(option)
    })
  },
  // 
  // { x: x, y: y, level: level }
  setViewCenter: function (option) {
    this.checkLoadState(() => {
      mapHandle.getMap().setViewCenter({ x: option.x, y: option.y, level: option.level })
    })
  },
  /**
  *   v0.0.1
  *  gps 
  *  1 layerId 
  **/
  pointsSlide: function (option) {
    var defaultOption = {
      layerId: 'realLayers', // id 
      speed: 2, // 
      ico: { // 
        // url: "assets/car1.png",
        // width: "40px",
        // height: "29px"
      },
      points: [ // 
        // {
        // id: "11",                     // 
        // x: 113.85891685966826,
        // y: 34.5658065000758
        // , ico: {
        //     url: "assets/car.png",
        //     width: "29px",
        //     height: "40px"
        //     isAngle: false            // 
        // }
        //  }
      ],
      url: '#', // http 
      isWindow: false // 
    }
    mapHandle.expand(defaultOption, option)
    mapHandle.getMap().pointsSlide(defaultOption)
  },
  // 
  heatMap: function (options) {
    var defaultOption = {
      pointlist: [],
      layerId: 'heatmaplayer',
      style: {
        colors: [
          { color: 'rgba(63, 40, 102, 0)', ratio: 0 },
          { color: 'rgb(0,0,255)', ratio: 0.25 },
          { color: 'rgb(0,255,0)', ratio: 0.55 },
          { color: 'yellow', ratio: 0.85 },
          { color: 'rgb(255,0,0)', ratio: 1 }
        ],
        blurRadius: 15,
        maxPixelIntensity: 25
      },
      onsuccess: ''
    }
    mapHandle.expand(defaultOption, options)
    mapHandle.getMap().builderHeatLayer(defaultOption)
  },
  // 
  constslayer: {
    defaultLayer: 'glayer', // 
    realPoliceLayer: 'realPoliceLayer', // 
    realCarLayer: 'realCarLayer', // 
    cacheLayer: 'cacheLayer', // 
    videoLayer: 'videoLayer', // 
    addressLayer: 'addressLayer', // 
    mapPath: 'mapPath' // 
  },
  expand: function (source, clone) {
    if (typeof (source) === 'object' && typeof (clone) === 'object') {
      for (var attr in clone) {
        if (typeof (clone[attr]) !== 'undefined') {
          if (typeof (clone[attr]) === 'object') {
            source[attr] = this.expand(source[attr], clone[attr])
          } else {
            source[attr] = clone[attr]
          }
        }
      }
    } else {
      source = clone
    }
    return source
  }
}

export default mapHandle



